.navigation {
  height: 106px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #eeebe3;
  transition: background 0.5s ease-out, color 0.25s ease-out; }

.navigation div {
  flex: 0 1 auto; }

.left-nav {
  margin-left: 24px; }

.right-nav {
  margin-right: 24px; }

.right-nav li,
.left-nav li {
  font-size: 14px;
  margin: 0 14px;
  display: inline-block;
  flex-grow: 0;
  flex-basis: 240px; }

.mobile {
  display: none; }

.opaque-nav {
  box-shadow: 0 0.1rem #eecfc8;
  background: #f1ebdd;
  color: #1d1d1d; }

.search-results {
  font-size: 16px; }
  .search-results .section-label {
    line-height: 2.5em;
    width: 100%;
    border-bottom: 1px solid #cfcac2;
    margin: 0 0 40px; }

.sproduct-list {
  width: 100%;
  display: grid;
  grid-gap: 40px;
  justify-content: space-evenly;
  grid-template-columns: repeat(4, minmax(110px, 380px)); }
  .sproduct-list .product-item .product-img {
    width: 100%;
    max-width: 100%;
    transition: transform 0.25s ease-out; }
  .sproduct-list .product-item .product-img:hover {
    transform: translate(2px, 2px); }
  .sproduct-list .product-item .product-title {
    overflow: hidden; }

@media (max-width: 640px) {
  .search-results {
    padding-top: 0; }
  .sproduct-list {
    grid-template-columns: 1fr;
    grid-gap: 20px; }
    .sproduct-list .product-item {
      display: grid;
      grid-template-columns: 70px 1fr;
      grid-template-rows: 37px;
      grid-template-areas: "product-img product-title" "product-img product-price";
      column-gap: 40px; }
      .sproduct-list .product-item .product-img {
        grid-area: product-img; }
      .sproduct-list .product-item .product-title {
        text-align: left;
        margin-top: 2px;
        white-space: nowrap;
        overflow: hidden;
        grid-area: product-title; }
      .sproduct-list .product-item .product-price {
        text-align: left;
        margin-top: 2px;
        grid-area: product-price; } }

@media (max-width: 1220px) {
  .left-nav {
    margin-left: 8px; }
  .right-nav {
    margin-right: 8px; }
  .left-nav li,
  .right-nav li {
    margin: 0; }
  .mobile {
    display: inline-block; }
  .default {
    display: none; }
  .center-nav {
    position: relative !important;
    top: 4px !important;
    left: 4px !important; } }
